<template>
  <div id="new">
    <h3>new Employe</h3>
    <div class="row">
      <form @submit.prevent="saveEmployee" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input v-model="employee_id" type="text" class="validate">
            <label for="first_name">Employee Id</label>
          </div>
          <div class="input-field col s12">
            <input id="last_name" type="text" class="validate" v-model="name">
            <label for="last_name">Name</label>
          </div>
          <div class="input-field col s12">
            <input id="last_name" type="text" class="validate" v-model="dept">
            <label for="last_name">Departement</label>
          </div>
          <div class="input-field col s12">
            <input id="last_name" type="text" class="validate" v-model="position">
            <label for="last_name">Position</label>
          </div>
        </div>
        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "NewEmployee",
  data() {
    return {
      employee_id: "",
      name: "",
      dept: "",
      position: ""
    };
  },
  methods: {
    saveEmployee() {
      db.collection("employees")
        .add({
          employee_id: this.employee_id,
          name: this.name,
          dept: this.dept,
          position: this.position
        })
        .then(docRef => {
          this.$router.push("/");
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

