<template>
  <div id="view">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{name}}</h4>
      </li>
      <li class="collection-item">Id : {{employee_id}}</li>
      <li class="collection-item">Departement : {{dept}}</li>
      <li class="collection-item">Position : {{position}}</li>
    </ul>
    <router-link to="/" class="btn">Back</router-link>
    <button class="btn red" @click="deleteEmployee">Delete</button>
    <div class="fixed-action-btn">
      <router-link
        :to="{name : 'edit',params:{id:this.$route.params.id}}"
        class="btn-floating btn-large red"
      >
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "ViewEmployee",
  data() {
    return {
      employee_id: "",
      name: "",
      dept: "",
      position: ""
    };
  },
  created() {
    db.collection("employees")
      .where("employee_id", "==", this.$route.params.id)
      .get()
      .then(query => {
        query.forEach(doc => {
          this.employee_id = doc.data().employee_id;
          this.name = doc.data().name;
          this.dept = doc.data().dept;
          this.position = doc.data().position;
        });
      });
  },
  methods: {
    deleteEmployee() {
      if (confirm("Are you sure you want to delete this employee ?")) {
        db.collection("employees")
          .where("employee_id", "==", this.$route.params.id)
          .get()
          .then(query => {
            query.forEach(doc => {
              doc.ref.delete();
              this.$router.push("/");
            });
          });
      }
    }
  }
};
</script>

