<template>
  <div id="edit">
    <h3>Edit</h3>
    <div class="row">
      <form @submit.prevent="editEmployee" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input v-model="employee_id" type="text" class="validate" disabled>
          </div>
          <div class="input-field col s12">
            <input id="last_name" type="text" class="validate" v-model="name">
          </div>
          <div class="input-field col s12">
            <input id="last_name" type="text" class="validate" v-model="dept">
          </div>
          <div class="input-field col s12">
            <input id="last_name" type="text" class="validate" v-model="position">
          </div>
        </div>
        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "EditEmployee",
  data() {
    return {
      employee_id: "",
      name: "",
      dept: "",
      position: ""
    };
  },
  created() {
    db.collection("employees")
      .where("employee_id", "==", this.$route.params.id)
      .get()
      .then(query => {
        query.forEach(doc => {
          this.employee_id = doc.data().employee_id;
          this.name = doc.data().name;
          this.dept = doc.data().dept;
          this.position = doc.data().position;
        });
      });
  },
  methods: {
    editEmployee() {
      db.collection("employees")
        .where("employee_id", "==", this.$route.params.id)
        .get()
        .then(query => {
          query.forEach(doc => {
            doc.ref.update({
              employee_id: this.employee_id,
              name: this.name,
              dept: this.dept,
              position: this.position
            });
          });
        })
        .then(() => {
          this.$router.push({
            name: "view",
            params: { id: this.$route.params.id }
          });
        });
    }
  }
};
</script>

